@import "/src/Resources/scss/variables/_variables.scss";
@import "/src/Resources/scss/customs-mixin.scss";

#AuthForm {
    @include centeredColumn;

	min-height: 100vh;
	max-height: 100vh;
    background-image: url("../../Resources/images/LoginBackGround.png");

    .title {
        color: $primary;
        margin-bottom: 3px;
        font-weight: 500;
        font-size: 1.563rem;
        font-style: normal;
        line-height: 1.875rem;
    }

    .text {
        font-size: 0.938rem;
        color: #5F5974;
        line-height: 1.125rem;
        font-weight: normal;
    }


    p {
        margin-bottom: 0px
    }

    .card-style {
        @include centeredColumn;

        padding: 0.313rem;
        background-color: black;
    }

    .card-body-style {
        width: 100%;
        padding: 1.58rem;
        border-radius: 0.313rem;
        background-color: #fff;
    }

    .form {
        width: 100%;
    }

    .btn-submit {
        width: 100%;
    }

    .footer-wrapper {
        @include centeredRow(5px);
        p {
            color: #fff !important;
        }
        margin: 25px 25px;
    }
}
