@mixin displayRow ( $gap : 0px, $justify : baseline, $align : baseline) {
    gap :  $gap;
	display: flex;
    flex-direction: row;
    align-items: $align;
    justify-content: $justify;
}

@mixin displayColumn ( $gap : 0px ) {
    gap :  $gap;
	display: flex;
    flex-direction: column;
}

@mixin centeredRow ( $gap : 0px ) {
    gap :  $gap;
	display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
}

@mixin centeredColumn ( $gap : 0px ) {
    gap :  $gap;
	display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

@mixin centeredProps {
    align-items: center;
    justify-content: center;
}